import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import { getColor, getBackgroundColor,  shade } from "../../styles/helper"



const propTypes = {
  
}








const ButtonComponent = ({children, onClick, className, left, right, ...rest}) => {
  return <button className={className} onClick={onClick} {...rest}>{children}</button>
}

const Button = styled(ButtonComponent)`
  padding: ${props => props.secondary ? "calc(0.75em - 3px) calc(1.5em - 3px)" : "0.75em 1.5em"};

  /* Text */
  font-size: ${props => props.theme.typography.button.fontSize}em;
  font-family: ${props => props.theme.typography.button.fontFamily};
  font-weight: ${props => props.theme.typography.button.fontWeight};
  letter-spacing: ${props => props.theme.typography.button.spacing}em;

  background: ${props => props.secondary ? "transparent" : props.theme.colors.primary};
  color: ${props => props.secondary ? props.theme.colors.primary : props.theme.colors.onPrimary};

  border: ${props => props.secondary ? "3px solid #00805D;" : "none"};
  outline: none;

  margin: ${props => {
    if(props.left) {
      return "auto auto auto 0"
    } else if(props.right) {
      return "auto 0 auto auto"
    } else {
      return "auto";
    }
  }};

  box-sizing: border-box;

  border-radius: 1.5em;

  transition: background 150ms ease-out, border-color 150ms ease-out, color 150ms ease-out;
  :hover {
    background: ${props => props.secondary ? "transparent" : props.theme.palette.primary["500"]};
    border-color: ${props => props.secondary ? props.theme.palette.primary["500"] : "none"};
    color: ${props => props.secondary ? props.theme.palette.primary["500"] : ""};

  }
  :active {
    background: ${props => props.secondary ? "transparent" : props.theme.palette.primary["600"]};
    border-color: ${props => props.secondary ? props.theme.palette.primary["600"] : "none"};
    color: ${props => props.secondary ? props.theme.palette.primary["600"] : ""};

  }
  
`

Button.propTypes = propTypes
export default Button